.card {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 30px;
  border: 1px solid #f3f3f3;
  width: 220px;
  border-radius: 40px;
  margin-right: 30px;
  margin-bottom: 30px;
  position: relative;
  transition: all 0.2s ease-out;
  transform: 0.2s ease-out;

  &:hover {
    box-shadow: 0px 20px 35px rgba(0, 0, 0, 0.05);
    transform: translateY(-0.2px);
  }

  .buttonLike {
    border: none;
    position: absolute;
    padding: 0;
    width: 32px;
    height: 32px;
    border-radius: 8px;
    box-shadow: -1px 1px 1px rgba(0, 0, 0, 0.1);
    cursor: pointer;
    transition: all 0.2s ease-out;

    &:hover {
      opacity: 1;
      transform: scale(1.1);
      box-shadow: -1px 1px 1px rgba(65, 62, 62, 0.2);
    }
  }
  .like {
    width: 32px;
    height: 32px;
    border: 1px solid rgba(65, 62, 62, 0.06);
    border-radius: 8px;
  }
  p {
    margin: 0;
  }
  h5 {
    box-sizing: border-box;
    font-size: 14px;
    font-weight: 500;
    margin: 6px 0;
  }
  p {
    opacity: 0.6;
    text-transform: uppercase;
    font-size: 12px;
  }
  .buttonAdd {
    margin-top: 10px;
    padding: 0;
    position: relative;
    background: #ffffff;
    box-sizing: border-box;
    border-radius: 8px;
    border: 1px solid #f2f2f2;
    width: 32px;
    height: 32px;
    cursor: pointer;
    box-shadow: -1px 1px 1px rgba(0, 0, 0, 0.1);
    transition: all 0.2s ease-out;

    &:hover {
      opacity: 1;
      transform: scale(1.1);
      box-shadow: -1px 1px 1px rgba(65, 62, 62, 0.2);
    }
    .buttonAddImg {
      width: 11px;
      height: 11px;
    }
    .buttonAddedImg {
      position: absolute;
      top: 0;
      right: 0;
      width: 32px;
      height: 32px;
    }
  }
}
