.containerEmpty {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    box-sizing: border-box;
    margin: 70px auto;
    img {
      margin-bottom: 80px;
    }
    h1 {
      margin: 0;
      opacity: 0.5;
    }
    h3 {
      opacity: 0.4;
      text-align: center;
      margin: 30px 0 0 0;
    }
    .backToShopButton {
      position: relative;
      margin-top: 40px;
      width: 80%;
      height: 45px;
      border: none;
      border-radius: 18px;
      background-color: #acde6f;
      cursor: pointer;
      color: #ffffff;
      font-size: 16px;
      &:hover {
        img {
          transform: translateX(-4px);
        }
      }
      img {
        position: absolute;
        top: 14px;
        left: 23px;
        margin: 0;
        transform: rotate(180deg);
        transition: all 0.2s ease-in-out;
      }
      &:hover {
        transition: all 0.2s ease-in-out;
        background: lighten(#9dd558, 6%);
      }
      &:active {
        background: darken(#9dd558, 6%);
      }
    }
  }
